@import url('./fonts/font.css');
*{
  box-sizing: border-box;
}

body{
  margin:0px;
  padding:0px;
  background-color: #1E1E1E;
  color:#fff;
}

.main_container{
  display:flex;
}

.html_tag{
  color:#569CD6;
  font-family: 'Consolas';
}
 
.backward{
  z-index:-2
}

.details_container{
  display:flex;
  flex-direction:column;
  flex:2;
}

.var{
  color:#569CD6
}

.var_name{
  color:#4FC1FF;
}

.string{
  color:#CE9178;
}

.inbuilt_func{
  color:#4EC9B0;
}

.custom_func{
  color:#DCDCAA;
}

.tag_highlight{
  color:#C586C0;
}

.tag_symbol{
  color:#808080;
}

.import_name{
  color:#9CDCFE;
}

.num{
  color:#B5CEA8;
}

.comment{
  color:#6A9955;
}

