.sider_container{
    height: 100%;
    background-color: #333333;
    width: 65px;
    box-shadow:0px 8px 2px rgb(0 0 0 / 25%);
    z-index:5;
}

nav > ul{
    margin:0px;
    padding:0px;
}

nav > ul li{
    list-style-type: none;
    text-align: center;
    padding: 15px 0px;
    cursor: pointer;
}

.sider_container li:hover .icon{
    color:#007FD4;
}

.logo{
    font-family: 'Sofia', cursive;
    font-size: 31px;
    padding-top: 0px;
    padding-bottom: 0px;
}

.sider_container .icon{
    font-size: 1.5rem;
    color:rgba(255,255,255,0.4);
    transition: 0.5s ease;
}

.active{
    border-left: 3px solid #fff;
}

.sider_container .active .icon{
    color:#fff;
}

@media only screen and (max-width: 680px) {
    .sider_container{
        width: 48px;
    }
    .sider_container .icon{
        font-size: 1.3rem;
    }
    .logo{
        font-size: 28px;
    }
}
